<template>
  <div class="login">
    <el-card class="login-card">
     <div class="login-img">
       <img src="../../assets/img/logo.271e845.png" alt="">
     </div>
      <el-form :model="formData" :rules="rules" ref="loginForm" status-icon>
       <el-form-item prop="mobile">
         <el-input v-model="formData.mobile" class="phone" placeholder="请输入手机号" ></el-input>
       </el-form-item>
        <el-form-item prop="code">
         <el-input v-model="formData.code" class="verify" placeholder="请输入验证码"></el-input>
          <el-button class="fa">发送验证码</el-button>
       </el-form-item>
        <el-form-item prop="check">
          <el-checkbox v-model="formData.check">我已阅读并同意用户协议和隐私条款</el-checkbox>
        </el-form-item>
        <el-form-item>
            <el-button  @click="login" class="btn" type="danger">登录</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>

export default {
  data () {
    var func = function (rule, value, callback) {
      // rule是当前的校验规则 没什么用
      // value就是当前表单的字段的值
      if (value) {
        // 满足校验
        callback() // 继续往下执行
      } else {
        // 不满足执行下面回调函数 显示一个错误
        callback(new Error('您需要勾选用户协议'))
      }
    }
    return {
      formData: {
        mobile: '',
        code: '',
        check: false

      },
      rules: {
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' }, // required: true,必填项,如果不填 就无法通过校验
          {
            len: 11, //  输入长度为11位  len表示要验证字段的确切长度，请指定len属性
            message: '手机长度必须为11个字符', // 当不满足设置的规则时的提示信息
            trigger: 'blur'
          },
          {
            pattern: /^1[3456789]\d{9}$/, //  电话格式
            message: '手机号格式错误', // 当不满足设置的规则时的提示信息
            trigger: 'blur'
          }
        ],
        code: [
          {
            required: true, // required: true,必填项,如果不填 就无法通过校验
            message: '验证码不能为空' // 当不满足设置的规则时的提示信息
          },
          {
            pattern: /^\d{6}$/, // 验证码格式
            message: '验证码长度必须为6个数字', // 当不满足设置的规则时的提示信息
            trigger: 'blur'
          }
        ],
        check: [{
          validator: func
        }
        ]
      }
    }
  },
  methods: {
    login () {
      // validate  手动校验表单数据
      // isOK是 当前表单是否通过了校验
      // result是当前的校验结果
      this.$refs.loginForm.validate(isOK => {
        if (isOK) {
          // 通过了校验
          this.$http({
            method: 'post',
            url: '/authorizations',
            data: this.formData // post参数是在data中写入的
          }).then(result => {
            window.localStorage.setItem('user-info', JSON.stringify(result.data))
            // 编程式导航
            this.$router.push('/home')
          }).catch(result => {
            this.$message({
              message: '您输入的账号或密码不存在',
              type: 'warning'
            })
          })
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.login {
   width: 100%;
    height: 100vh;
    background: url('../../assets/img/456.jpg') no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    .login-card {
      width: 500px;
      height: 380px;
      border-radius: 0;
      // text-align: center;
      padding: 0 30px;
      box-sizing: border-box;
    }
    .login-img {
      text-align: center;
      img {
        width: 220px;
        height: 50px;
      }
    }
    .phone {
      margin: 15px 0;
    }
    .verify {
      width: 65%;
    }
    .fa{
      float: right;
    }
    .btn {
      width: 100%;
    }
}
</style>
