import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/home'
import Login from './views/login'
import Main from './views/home/mian.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'home',
      component: Home,
      children: [{
        path: '',
        component: Main // 二级路由
      },
      {
        // 评论列表
        path: '/home/comment',
        component: () => import('./views/commont/index.vue') // 按需加载
      },
      {
        // 素材列表
        path: '/home/material',
        component: () => import('./views/material/index.vue') // 按需加载
      },
      {
        // 内容列表
        path: '/home/articles',
        component: () => import('./views/articles/index.vue') // 按需加载
      },
      {
        // 发布列表
        path: '/home/publish',
        component: () => import('./views/publish/index.vue') // 按需加载
      },
      {
        // 账户信息
        path: '/home/account',
        component: () => import('./views/account/index.vue') // 按需加载
      },
      {
        // 粉丝列表 图文数据
        path: '/home/fans',
        component: () => import('./views/fans/index.vue') // 按需加载
      }

      ]
    },
    {
      path: '/',
      redirect: '/login'
    },
    {
      // 通配符 匹配找不到家的路由
      path: '*',
      component: () => import('./views/404.vue') // 按需加载 // 找不到路由
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    }
    // {
    //   path: '/about',
    //   name: 'about',
    //   // route level code-splitting
    //   // this generates a separate chunk (about.[hash].js) for this route
    //   // which is lazy-loaded when the route is visited.
    //   component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    // }
  ]
})
