import Vue from 'vue'
import App from './App.vue'
import router from './permission'
import ElementUI from 'element-ui' // 引入element-ui
import Component from './components/index' // 引入自定义组件
import 'element-ui/lib/theme-chalk/index.css' // 引入element-ui样式
import './assets/styles/index.less' // 引入重置样式

import 'nprogress/nprogress.css' // 进度条 这个样式必须引入

import axios from './utils/axios-config' // 自定义axios
Vue.prototype.$http = axios // 给axios设置一个全局属性
Vue.config.productionTip = false
Vue.use(ElementUI) // 使用elementUI
Vue.use(Component) // 使用自定义全局组件
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
