import slotHome from './home/slothome.vue'
import slotHeader from './home/slotheader'
import slotCommon from './common/common.vue'
// elementUI导出的对象 对象里有一个方法 install
// Vue.use(elementUI) => 调用其提供的install方法，并且在方法中传入Vue对象
export default {
  install (Vue) {
    Vue.component('slot-home', slotHome) // 注册了slot-home组件
    Vue.component('slot-header', slotHeader) // 注册了slot-header组件
    Vue.component('slot-common', slotCommon)
  }
}
