<template>
    <div>
    <el-row type="flex" justify="space-between" class="slot-header">
          <el-col :span="16" class="left-title">
              <i @click="isFold" :class="fontImg"></i>
                <span style="margin-left:10px;">北京今日头条科技有限公司</span>
            </el-col>
         <el-col :span="7" class="right-title">
             <!-- 搜索框 -->
            <el-tooltip content="搜索" >
            <!-- <el-button>消息</el-button> -->
            <el-input class="seach" placeholder="请输入内容" >
                <i class="el-icon-search el-input__icon" slot="prefix"></i>
            </el-input>
           </el-tooltip>
             <!-- 消息弹框 -->
            <el-tooltip class="item" content="消息">
            <span>消息</span>
           </el-tooltip>
           <!-- 头像 -->
             <img :src="user.photo ? user.photo : defineImg" alt="">
              <el-dropdown trigger="click" class="right" @command="commandAction">
                 <span class="el-dropdown-link">
                  {{user.name}}<i class="el-icon-arrow-down el-icon--right"></i>
             </span>
             <!-- 下拉列表 -->
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="account">个人信息</el-dropdown-item>
                <el-dropdown-item command="git">git地址</el-dropdown-item>
                <el-dropdown-item command="out">退出</el-dropdown-item>
             </el-dropdown-menu>
             </el-dropdown>
         </el-col>
    </el-row>
    </div>
</template>

<script>
import evenBus from '../../utils/evenBus.js'
export default {
  data () {
    return {
      user: {},
      fontImg: 'el-icon-s-unfold',
      defineImg: require('../../assets/img/avatar.jpg') // requier将突变地址转变成64位
    }
  },
  methods: {
    // 是否折叠
    isFold () {
      evenBus.$emit('openOrClose')
      this.fontImg = this.fontImg === 'el-icon-s-unfold' ? 'el-icon-s-fold' : 'el-icon-s-unfold'
      // 点击切换class样式
    },
    //   点击菜单选项会触发事件
    //  command是点击的菜单选项command属性值
    commandAction (command) {
      // console.log(command)
      if (command === 'account') {
        this.$router.push('/home/account')
      } else if (command === 'git') {
        // 跳转到git地址
        window.location.href = 'https://github.com/yanwanpeng666/toutiao'
      } else {
        // 退出到login
        window.localStorage.clear() // 清除本项目在浏览器上所有的前端存储
        this.$router.push('/login') // 跳转到login登录界面
      }
    },
    async getUser () {
      let result = await this.$http({
        // 不写method 默认是get类型
        url: '/user/profile'
      })
      this.user = result.data
    }
  },
  created () {
    this.getUser()
    evenBus.$on('updateUserInfoSuccess', () => {
      //  监听更新数据
      this.getUser()
    })
  }
}
</script>

<style lang='less' scoped>
.slot-header {
  padding: 12px 0;

  .left-title {
    display: flex;
    align-items: center;
    i {
      font-size: 20px;
      margin-right: 4px;
    }
  }

  .right-title {
    display: flex;
    align-items: center;
    .seach {
      width: 200px;
      margin-right: 20px;
    }
    img {
      width: 45px;
      height: 45px;
      border-radius: 50%;
      margin: 0 20px;
    }
    .right {
      cursor: pointer;
    }
  }
}
</style>
